'use client';

import { createContext, useContext, useEffect, useState } from 'react';
import {
    onAuthStateChanged,
    signInWithPopup,
    GoogleAuthProvider,
    FacebookAuthProvider,
    createUserWithEmailAndPassword,
    signInWithEmailAndPassword,
    sendEmailVerification,
    signOut as firebaseSignOut,
    User
} from 'firebase/auth';
import { auth, db } from '@/lib/firebase';
import { doc, getDoc, setDoc } from 'firebase/firestore';

interface AuthContextType {
    user: User | null;
    loading: boolean;
    signInWithGoogle: () => Promise<void>;
    signInWithFacebook: () => Promise<void>;
    registerWithEmail: (email: string, pass: string, name: string) => Promise<User | undefined>;
    loginWithEmail: (email: string, pass: string) => Promise<void>;
    signOut: () => Promise<void>;
}

const AuthContext = createContext<AuthContextType>({} as AuthContextType);

export const useAuth = () => useContext(AuthContext);

export const AuthContextProvider = ({ children }: { children: React.ReactNode }) => {
    const [user, setUser] = useState<User | null>(null);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        if (!auth) {
            // Mock auth for guest mode if keys are missing
            console.warn("Auth not initialized (missing keys). Using Guest Mode.");
            setLoading(false);
            return;
        }

        const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
            setUser(currentUser);
            setLoading(false);
        });
        return () => unsubscribe();
    }, []);

    const signInWithGoogle = async () => {
        if (!auth || !db) {
            alert("No Firebase API Keys found! Check console.");
            return;
        }

        const provider = new GoogleAuthProvider();
        try {
            const result = await signInWithPopup(auth, provider);
            const user = result.user;

            // Check if user exists in Firestore
            const userRef = doc(db, 'users', user.uid);
            const userSnap = await getDoc(userRef);

            if (!userSnap.exists()) {
                // Create new user profile if not exists
                await setDoc(userRef, {
                    uid: user.uid,
                    displayName: user.displayName,
                    email: user.email,
                    photoURL: user.photoURL,
                    rank: 'Rookie',
                    exp: 0,
                    coins: 100, // Welcome bonus
                    partyId: null,
                    createdAt: new Date()
                });
                console.log("New adventurer registered!");
            }
        } catch (error) {
            console.error("Error signing in with Google", error);
            alert("Login Failed: " + (error as any).message);
        }
    };

    const signInWithFacebook = async () => {
        if (!auth || !db) {
            alert("No Firebase Keys! Cannot login with Facebook real account.");
            return;
        }
        const provider = new FacebookAuthProvider();
        try {
            const result = await signInWithPopup(auth, provider);
            const user = result.user;

            // Check if user exists in Firestore
            const userRef = doc(db, 'users', user.uid);
            const userSnap = await getDoc(userRef);

            if (!userSnap.exists()) {
                // Create new user profile
                await setDoc(userRef, {
                    uid: user.uid,
                    displayName: user.displayName || 'Facebook Adventurer',
                    email: user.email,
                    photoURL: user.photoURL,
                    rank: 'Rookie',
                    exp: 0,
                    coins: 100, // Welcome bonus
                    partyId: null,
                    createdAt: new Date()
                });
            }
        } catch (error) {
            console.error("Error signing in with Facebook", error);
            alert("Facebook Login Failed: " + (error as any).message);
        }
    };

    const registerWithEmail = async (email: string, pass: string, name: string) => {
        if (!auth || !db) return;
        try {
            const result = await createUserWithEmailAndPassword(auth, email, pass);
            const user = result.user;

            // Send Verification Email
            await sendEmailVerification(user);

            // Create user profile
            await setDoc(doc(db, 'users', user.uid), {
                uid: user.uid,
                displayName: name || 'Adventurer',
                email: user.email,
                photoURL: '',
                rank: 'Rookie',
                exp: 0,
                coins: 100,
                partyId: null,
                createdAt: new Date()
            });

            return user;
        } catch (error) {
            console.error("Registration Error:", error);
            throw error;
        }
    };

    const loginWithEmail = async (email: string, pass: string) => {
        if (!auth) return;
        try {
            await signInWithEmailAndPassword(auth, email, pass);
        } catch (error) {
            console.error("Login Error:", error);
            throw error;
        }
    };

    const signOut = async () => {
        if (!auth) {
            setUser(null);
            return;
        }
        try {
            await firebaseSignOut(auth);
        } catch (error) {
            console.error("Error signing out", error);
        }
    };

    return (
        <AuthContext.Provider value={{ user, loading, signInWithGoogle, signInWithFacebook, registerWithEmail, loginWithEmail, signOut }}>
            {children}
        </AuthContext.Provider>
    );
};
